.about__container {
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
  padding: 20px;
  border-radius: 15px;
  background-color: white;
  margin: 0 auto;
  margin-top: 100px;
  max-width: 25%;
  font-weight: 300;
}

.title {
  font-size: 32px;
  font-weight: 700;
  color: rgb(135, 150, 235);
  margin-bottom: 20px;
}

a {
  text-align: center;
}

li {
  list-style: none;
}

img {
  width: 100%;
  height: 100%;
}

div {
  text-align: center
}